import simpleAddress from '@/components/common/simple-address/simple-address.vue';
// import simpleAddress from"../common/simple-address/simple-address.vue"
function getDate(type) {
	const date = new Date();

	let year = date.getFullYear();
	let month = date.getMonth() + 1;
	let day = date.getDate();

	if (type === 'start') {
		year = year - 60;
	} else if (type === 'end') {
		year = year + 2;
	}
	month = month > 9 ? month : '0' + month;;
	day = day > 9 ? day : '0' + day;

	return `${year}-${month}-${day}`;
}
export default{
	components: {
		simpleAddress
	},
	data(){
		return{
			xblist:['男','女'],
			xbcheck:0,
			date: getDate({
				format: true
			}),
			startDate:getDate('start'),
			endDate:getDate('end'),
			userId:null,
			userInfo:{},
			imgUrl:'',
			photo:"",
			cityPickerValueDefault: [0, 0, 1],
			// addressList:["湖北省","武汉市","江夏区"],
			addressList:[],
			codeList:[],
			pickerText:"",
			gxlist:["父亲","母亲","爷爷","奶奶","叔叔","阿姨"],
			gxcheck:0,
			btnStr:'获取验证码',
			codeFlag:true,
			regCode:'',
		}
	},
	onLoad(e){
		console.log(e.id)
		this.imgUrl=this.$imgurl
		this.userId=e.id
		let userInfo=uni.getStorageSync("userInfo")
		userInfo=JSON.parse(userInfo)
		this.photo=userInfo.photo
		this.getUserInfo()
	},	
	created(){
	},
	onShow(){
	},
	methods:{
		getDwCode(){
			var index = this.$refs.simpleAddress.queryIndex(this.addressList, 'label');
			console.log(index);
			var data=index.data
			this.codeList[0]=data.province.value
			this.codeList[1]=data.city.value
			this.codeList[2]=data.area.value
			console.log(this.codeList)
		},
		openAddres2() {
			// 根据 label 获取
			var index = this.$refs.simpleAddress.queryIndex(this.addressList, 'label');
			console.log(index);
			this.cityPickerValueDefault = index.index;
			this.$refs.simpleAddress.open();
		},
				
		onConfirm(e) {
			this.pickerText = JSON.stringify(e);
			console.log("这呃呃呃呃呃呃")
			console.log(e)
			this.addressList=e.labelArr
			this.codeList[0]=e.provinceCode
			this.codeList[1]=e.cityCode
			this.codeList[2]=e.areaCode
			console.log(this.codeList)
		},
		xbchange(val){
			console.log(val)
		},
		gxchange(val){
			// 关系改变
			console.log(val)
			this.userInfo.relation=this.gxlist[val.detail.value]
			this.gxcheck=val.detail.value
			console.log(this.userInfo.relation)
		},
		bindDateChange: function(e) {
			this.date = e.detail.value
		},
		bindTimeChange: function(e) {
			this.time = e.detail.value
		},
		getUserInfo(){
			this.$http.post("puparent/detail",{
				parentId:this.userId
			}).then(res=>{
				if(res.code==100){
					this.userInfo=res.info
					let data=res.info
					// this.addressList=[]
					this.addressList[0]=data.province.name
					this.addressList[1]=data.city.name
					this.addressList[2]=data.area.name
					this.getDwCode()
					// this.gxcheck
					let index=0
					if(data.relation){
						index=this.gxlist.indexOf(data.relation)
					}
					this.gxcheck=index==-1?0:index
				}
			})
		},
		getCodenum(){
			//获取验证码
			console.log("获取验证码")
			if(this.codeFlag){
				console.log('验证')
				console.log(/^1[3456789]\d{9}$/.test(this.userInfo.phone))
				 if(!(/^1[3456789]\d{9}$/.test(this.userInfo.phone))){ 
					 this.codeFlag=false
					 uni.showToast({
						 icon:"none",
						 title:"请输入正确的手机号码！"
					 })
				 }
			}
			if(this.codeFlag){
				//可以获取
				this.codeFlag=false
				uni.showLoading({
					icon:"loading",
					title:"正在发送！"
				})
				this.$http.post("puparent/produceCode",{
					phone:this.userInfo.phone
				}).then(res=>{
						uni.hideLoading();
					if(res.code==100){
						uni.showLoading({
							icon:"success",
							title:"发送成功！",
							success:()=>{
								setTimeout(()=>{
									uni.hideLoading();
								},2000)
							}
						})
						//获取成功倒计时
						let timer=null
						let count=60
						timer=setInterval(()=>{
								this.btnStr=count+'s后重新获取'
								count--
								if(count==0){
									clearInterval(timer)
									this.codeFlag=true
									this.btnStr="获取验证码"
								}
						},1000)
						
					}else{
						// 发送失败
						uni.hideLoading()
						uni.showToast({
							icon:"none",
							title:"发送验证码失败！"
						})
						this.codeFlag=true
					}
				})
				
			}else{
				//不可以获取
				 if((/^1[3456789]\d{9}$/.test(this.phone))){ 
					 uni.showToast({
					 	title:'正在获取，请稍后！',
					 	icon:'none'
					 })
				 }else{
					 this.codeFlag=true
				 }				
			}
		},
		update(){
			// 修改  验证验证码是否正确
			if(this.regCode){
				this.$http.post("puparent/verification",{
					phone:this.userInfo.phone,
					code:this.regCode,						
				}).then(res=>{
					if(res.code==100){
						this.submitInfo()
					}else{
						uni.showToast({
							icon:"none",
							title:res.msg
						})
					}
				})
			}else{
				uni.showToast({
					icon:"none",
					title:"请输入验证码"
				})
			}
		},
		submitInfo(){
			console.log("提交")
			this.$http.post("puparent/update",{
				id:this.userInfo.id,
				phone:this.userInfo.phone,
				name:this.userInfo.name,
				relation:this.gxlist[this.gxcheck],
				detailAddreee:this.userInfo.detailAddreee,
				province:{
					code:this.codeList[0],
					name:this.addressList[0]
				},
				city:{
					code:this.codeList[1],
					name:this.addressList[1],
				},
				area:{
					code:this.codeList[2],
					name:this.addressList[2],
				}
			},'application/json').then(res=>{
				if(res.code==100){
					uni.showToast({
						icon:"success",
						title:"修改成功"
					})
					setTimeout(()=>{
						uni.navigateBack({
							
						})
					},2000)
				}
			})
		},
	}
}
